<template>
  <div>
    <el-row :gutter="12">
      <el-col :span="8" v-for="(item,index) in cardList" :key="index">
        <el-card :shadow="item.showdow">{{item.name}}</el-card>
      </el-col>
    </el-row>
  </div>
</template>

<script>
export default {
  name: "MainPage",
  data() {
    return {
      cardList: [
        { name: "測試1", showdow: "always" },
        { name: "測試2", showdow: "always" },
        { name: "測試3", showdow: "always" }
      ]
    };
  }
};
</script>


